import axios from "axios";
import {
  STAFF_LOGIN_REQUEST,
  STAFF_LOGIN_SUCCESS,
  STAFF_LOGIN_FAIL,
  STAFF_LOGOUT,
  STAFF_DETAILS_REQUEST,
  STAFF_DETAILS_SUCCESS,
  STAFF_DETAILS_FAIL,
  STAFF_APPROVE_V_FAIL,
  STAFF_APPROVE_V_REQUEST,
  STAFF_APPROVE_V_SUCCESS,
  STAFF_DELAY_V_FAIL,
  STAFF_DELAY_V_REQUEST,
  STAFF_DELAY_V_SUCCESS,
  STAFF_REJECT_V_FAIL,
  STAFF_REJECT_V_REQUEST,
  STAFF_REJECT_V_SUCCESS,
} from "../constants/staffConstants";

export const staffAuth = (id) => async (dispatch) => {
  try {
    dispatch({ type: STAFF_LOGIN_REQUEST });

    const config = {
      headers: {
        "Content-Type": "application/json",
        "Access-Control-Allow-Origin": "*",
      },
    };
    const { data } = await axios.post(`/api/v1/employee/login`, { id }, config);

    dispatch({
      type: STAFF_LOGIN_SUCCESS,
      payload: data,
    });
    localStorage.setItem("staffInfo", JSON.stringify(data));
  } catch (error) {
    dispatch({
      type: STAFF_LOGIN_FAIL,
      payload:
        error.response && error.response.data.error
          ? error.response.data.error
          : error.message,
    });
  }
};

export const staffLogout = () => (dispatch) => {
  localStorage.removeItem("staffInfo");
  dispatch({ type: STAFF_LOGOUT });
};

export const getStaffDetails = () => async (dispatch, getState) => {
  try {
    dispatch({ type: STAFF_DETAILS_REQUEST });

    const {
      staffLogin: { staffInfo },
    } = getState();

    const config = {
      headers: {
        "Content-Type": "application/json",
        "Access-Control-Allow-Origin": "*",
        Authorization: `Bearer ${staffInfo.token}`,
      },
    };
    const { data } = await axios.get(`/api/v1/employee/me`, config);

    dispatch({
      type: STAFF_DETAILS_SUCCESS,
      payload: data,
    });
  } catch (error) {
    dispatch({
      type: STAFF_DETAILS_FAIL,
      payload:
        error.response && error.response.data.message
          ? error.response.data.message
          : error.message,
    });
  }
};

