import {
  LOG_FAIL,
  LOG_REQUEST,
  LOG_SUCCESS,
  STAFF_LOGS_FAIL,
  STAFF_LOGS_REQUEST,
  STAFF_LOGS_SUCCESS,
} from "../constants/logConstants";

export const logReducer = (
  state = {
    logs: {},
    newGuest: [],
    awaiting: [],
    today: [],
    allLogs: [],
    approved: [],
    rejected: [],
  },
  action
) => {
  switch (action.type) {
    case LOG_REQUEST:
      return { ...state, loading: true };
    case LOG_SUCCESS:
      return {
        loading: false,
        logs: action.payload,
        newGuest: action.payload.newGuest,
        awaiting: action.payload.awaiting,
        today: action.payload.today,
        allLogs: action.payload.allLogs,
        approved: action.payload.approved,
        rejected: action.payload.rejected,
        vin: action.payload.vin,
        out: action.payload.out,
        success: true,
      };
    case LOG_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};

export const staffLogReducer = (
  state = {
    logs: {
      vtoday: 0,
      pending: 0,
      checkedIn: 0,
      checkedOut: 0,
      book: 0,
      staff: 0,
      all: 0,
      admin: 0,
    },
    newGuest: [],
    awaiting: [],
    today: [],
    allLogs: [],
    approved: [],
    rejected: [],
    booked: [],
    allStaff: [],
    allAdmin: [],
  },
  action
) => {
  switch (action.type) {
    case STAFF_LOGS_REQUEST:
      return { ...state, loading: true };
    case STAFF_LOGS_SUCCESS:
      return {
        loading: false,
        logs: action.payload,
        newGuest: action.payload.newGuest,
        awaiting: action.payload.awaiting,
        today: action.payload.today,
        allLogs: action.payload.allLogs,
        approved: action.payload.approved,
        rejected: action.payload.rejected,
        booked: action.payload.booked,
        allStaff: action.payload.allStaff,
        allAdmin: action.payload.allAdmin,
        success: true,
      };
    case STAFF_LOGS_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};
