import React, { useEffect } from "react";
import Navbar from "./Navbar";
import styles from "./styles.module.css";
import {
  CircularProgress,
  Alert,
  AlertIcon,
  Center,
  useToast,
} from "@chakra-ui/react";
import { useDispatch, useSelector } from "react-redux";
import { getSingleGuest } from "../../redux/actions/guestActions";
import { Link, useNavigate, useParams } from "react-router-dom";
import { approveV, delayV, rejectV } from "../../redux/actions/staffActions";
import {
  STAFF_APPROVE_V_RESET,
  STAFF_DELAY_V_RESET,
  STAFF_REJECT_V_RESET,
} from "../../redux/constants/staffConstants";
import { getStafflogs } from "../../redux/actions/logActions";

const MyGuest = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { id } = useParams();
  const toast = useToast();
  // const id = match.params.id;
  const singleGuest = useSelector((state) => state.singleGuest);
  const { loading, error, info } = singleGuest;

  const staffApprove = useSelector((state) => state.staffApprove);
  const { loading: sLoading, error: sError, success } = staffApprove;

  const staffReject = useSelector((state) => state.staffReject);
  const { loading: rLoading, error: rError, success: rSuccess } = staffReject;

  const staffDelay = useSelector((state) => state.staffDelay);
  const { loading: dLoading, error: dError, success: dSuccess } = staffDelay;

  useEffect(() => {
    dispatch(getSingleGuest(id));
  }, [dispatch, id]);
  const name = info && info.user && info.user.fullname;
  const approveHandler = () => {
    dispatch(approveV(id, name));
  };

  const rejectHandler = () => {
    if (window.confirm("Are you sure you want to reject this visitor?")) {
      dispatch(rejectV(id, name));
    }
  };

  const delayHandler = () => {
    dispatch(delayV(id, name));
  };
  var date = new Date().toJSON().slice(0, 10).replace(/-/g, "/");

  if (success) {
    dispatch(getStafflogs(date));
    toast({
      title: "Notification",
      description: "Guest has been Approved",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: STAFF_APPROVE_V_RESET });
    navigate("/staff/dashboard");
  }

  if (rSuccess) {
    dispatch(getStafflogs(date));
    toast({
      title: "Notification",
      description: "Guest has been Rejected",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: STAFF_REJECT_V_RESET });
    navigate("/staff/dashboard");
  }

  if (dSuccess) {
    dispatch(getStafflogs(date));
    toast({
      title: "Notification",
      description: "Guest has been delayed",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: STAFF_DELAY_V_RESET });
    navigate("/staff/dashboard");
  }

  return (
    <div>
      <Navbar />
      <div className={styles.contents}>
        <div className={styles.overlay}>
          <small>Guest Information</small>
        </div>
        {loading ? (
          <Center>
            <CircularProgress isIndeterminate color="green.300" />
          </Center>
        ) : (
          <>
            {error && (
              <Alert>
                <AlertIcon />
                {error}
              </Alert>
            )}
            {sError && (
              <Alert>
                <AlertIcon />
                {sError}
              </Alert>
            )}
            {rError && (
              <Alert>
                <AlertIcon />
                {rError}
              </Alert>
            )}
            {dError && (
              <Alert>
                <AlertIcon />
                {dError}
              </Alert>
            )}

            {sLoading || rLoading || dLoading ? (
              <>
                <Center>
                  <CircularProgress isIndeterminate color="green.300" />
                </Center>
              </>
            ) : (
              <div className={styles.guestContainer}>
                <div className={styles.guestGrid}>
                  <div className={styles.guestCard}>
                    <img src={info && info.photo} alt={info && info._id} />
                  </div>
                  <div className={styles.guestCard}>
                    <Link to="/frontdesk/newGuest" className={styles.whiteBtn}>
                      Go Back
                    </Link>
                    <br />
                    <br />
                    <div className={styles.titleCard}>
                      <h2>Basic Information</h2>
                    </div>
                    <p>
                      <b>Name: </b>
                      {info && info.user && info.user.fullname}
                    </p>
                    <p>
                      <b>Company: </b>
                      {info && info.user && info.user.company}
                    </p>
                    <p>
                      <b>Email: </b>
                      {info && info.user && info.user.email}
                    </p>
                    <p>
                      <b>Mobile: </b>
                      {info && info.user && info.user.mobile}
                    </p>
                    <div className={styles.titleCard}>
                      <h2>Laptop Information</h2>
                    </div>

                    <p>
                      <b>Laptop: </b>
                      {info && info.laptop}
                    </p>
                    <p>
                      <b>Laptop SN: </b>
                      {info && info.lsn}
                    </p>
                    <div className={styles.titleCard}>
                      <h2>Whom to Visit</h2>
                    </div>

                    <p>
                      <b>Host: </b>
                      {info && info.host.fullname}
                    </p>
                    <p>
                      <b>Purpose of Visit </b>
                      {info && info.purpose}
                    </p>
                    <p>
                      <b>Appointment </b>
                      {info && info.appointment}
                    </p>
                    <p>
                      <b>Time-in </b>
                      {info && info.timeIn}
                    </p>
                    <p>
                      <b>Date </b>
                      {info && info.date}
                    </p>
                    <br />
                    <div className={styles.btnC}>
                      <button
                        className={`${styles.btn} ${styles.success}`}
                        onClick={approveHandler}
                      >
                        Approve
                      </button>
                    </div>
                    <div className={styles.btnC}>
                      <button
                        className={`${styles.btn} ${styles.danger}`}
                        onClick={rejectHandler}
                      >
                        Reject
                      </button>
                    </div>
                    <div className={styles.btnC}>
                      <button
                        className={`${styles.btn} ${styles.warning}`}
                        onClick={delayHandler}
                      >
                        Hold On
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            )}
          </>
        )}
      </div>
    </div>
  );
};

export default MyGuest;
