import React, { useState, useEffect } from "react";
import Navbar from "./Navbar";
import styles from "./styles.module.css";
import {
  CircularProgress,
  Alert,
  AlertIcon,
  Center,
  useToast,
  Table,
  Thead,
  Tbody,
  Tr,
  Th,
  Td,
  TableCaption,
  Button,
  Drawer,
  DrawerBody,
  DrawerFooter,
  DrawerHeader,
  DrawerOverlay,
  DrawerContent,
  DrawerCloseButton,
  useDisclosure,
} from "@chakra-ui/react";
import { useDispatch, useSelector } from "react-redux";
import { STAFF_REG_RESET } from "../../redux/constants/adminConstants";
import { addNewEmployee } from "../../redux/actions/adminActions";
import { getlogs } from "../../redux/actions/logActions";
import ReactHTMLTableToExcel from "react-html-table-to-excel";

const Staff = () => {
  const toast = useToast();
  const [fullname, setFullName] = useState("");
  const [email, setEmail] = useState("");
  const [mobile, setMobile] = useState("");
  const [designation, setDesignation] = useState("");
  const [msg, setMsg] = useState(false);

  const { isOpen, onOpen, onClose } = useDisclosure();
  const btnRef = React.useRef();

  const dispatch = useDispatch();

  const addStaff = useSelector((state) => state.addStaff);
  const { loading, error, success } = addStaff;

  const submitHandler = (e) => {
    e.preventDefault();
    if (!fullname || !email || !mobile || !designation) {
      setMsg(true);
    } else {
      setMsg(false);
      dispatch(addNewEmployee(fullname, email, mobile, designation));
    }
  };
  if (success) {
    toast({
      title: "Notification",
      description: "Staff has been added",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    onClose();
    dispatch({ type: STAFF_REG_RESET });
    var date = new Date().toJSON().slice(0, 10).replace(/-/g, "/");
    dispatch(getlogs(date));
  }
  const vmslogs = useSelector((state) => state.vmslogs);
  const { logs, loading: loadingLogs, error: errorLogs } = vmslogs;

  useEffect(() => {
    var date = new Date().toJSON().slice(0, 10).replace(/-/g, "/");
    dispatch(getlogs(date));
  }, [dispatch, date]);

  return (
    <div>
      <Navbar />
      <div className={styles.contents}>
        <div className={styles.overlay}>
          <small>Add Staff</small>
        </div>

        <Drawer
          isOpen={isOpen}
          placement="right"
          onClose={onClose}
          finalFocusRef={btnRef}
          size="md"
        >
          <DrawerOverlay />
          <DrawerContent>
            <DrawerCloseButton />
            <DrawerHeader>Create your account</DrawerHeader>

            <DrawerBody>
              {loading ? (
                <Center>
                  <CircularProgress isIndeterminate color="green.300" />
                </Center>
              ) : (
                <>
                  {error && (
                    <Alert status="error">
                      <AlertIcon />
                      {error}
                    </Alert>
                  )}
                  {msg && (
                    <Alert status="warning">
                      <AlertIcon />
                      All Fields are required
                    </Alert>
                  )}
                  <div className={styles.inputContainer}>
                    <label>Name</label>
                    <input
                      type="text"
                      onChange={(e) => setFullName(e.target.value)}
                      className="form-control"
                      value={fullname}
                    />
                  </div>
                  <div className={styles.inputContainer}>
                    <label>Designation</label>
                    <input
                      type="text"
                      onChange={(e) => setDesignation(e.target.value)}
                      className="form-control"
                      value={designation}
                    />
                  </div>

                  <div className={styles.inputContainer}>
                    <label>Email</label>
                    <input
                      type="email"
                      onChange={(e) => setEmail(e.target.value)}
                      className="form-control"
                      value={email}
                    />
                  </div>
                  <div className={styles.inputContainer}>
                    <label>Mobile</label>
                    <input
                      type="number"
                      onChange={(e) => setMobile(e.target.value.slice(0, 11))}
                      className="form-control"
                      value={mobile}
                    />
                  </div>
                </>
              )}
            </DrawerBody>

            <DrawerFooter>
              <Button variant="outline" mr={3} onClick={onClose}>
                Cancel
              </Button>
              <Button colorScheme="blue" type="button" onClick={submitHandler}>
                Add Staff
              </Button>
            </DrawerFooter>
          </DrawerContent>
        </Drawer>

        <div className={styles.guestContainer}>
          <Button ref={btnRef} colorScheme="teal" onClick={onOpen}>
            Add Staff
          </Button>
          <br />
          <br />
          <br />
          <hr />
          {loadingLogs ? (
            <Center>
              <CircularProgress isIndeterminate color="green.300" />
            </Center>
          ) : (
            <>
              {errorLogs && (
                <Alert>
                  <AlertIcon />
                  {errorLogs}
                </Alert>
              )}
              {logs && logs.allStaff.length <= 0 ? (
                <>
                  <Alert status="warning">
                    <AlertIcon />
                    No Guest Available
                  </Alert>
                </>
              ) : (
                <>
                  <Button colorScheme="yellow">
                    <ReactHTMLTableToExcel
                      id="test-table-xls-button"
                      className="download-table-xls-button"
                      table="table-to-xls"
                      filename="Visitors Log"
                      sheet="tablexls"
                      buttonText="Download as XLS"
                    />
                  </Button>
                  <Table variant="simple" id="table-to-xls">
                    <TableCaption>All Admins</TableCaption>
                    <Thead>
                      <Tr>
                        <Th>S/N</Th>
                        <Th>Name</Th>
                        <Th>Email</Th>
                        <Th>Mobile</Th>
                      </Tr>
                    </Thead>
                    <Tbody>
                      {logs &&
                        logs.allStaff.map((item, index) => (
                          <Tr key={index}>
                            <Td>{index + 1}</Td>
                            <Td>{item.fullname}</Td>
                            <Td>{item.email}</Td>
                            <Td>{item.mobile}</Td>
                          </Tr>
                        ))}
                    </Tbody>
                  </Table>
                </>
              )}
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default Staff;
