import { createStore, combineReducers, applyMiddleware } from "redux";
import thunk from "redux-thunk";
import { composeWithDevTools } from "redux-devtools-extension";
import {
  addReturningGuestReducer,
  bookedGuestReducer,
  guestReducer,
  returningGuestReducer,
  checkGuestReducer,
} from "./reducers/vmsReducers";
import {
  adminDetailsReducer,
  adminLoginReducer,
  adminRegisterReducer,
  addStaffReducer,
  getStaffReducer,
  deleteAdminReducer,
  editAdminReducer,
} from "./reducers/adminReducers";
import { logReducer, staffLogReducer } from "./reducers/logReducers";
import {
  checkinReducer,
  checkoutReducer,
  notifyHostReducer,
  prebookReducer,
  signoutReducer,
  singleGuestReducer,
} from "./reducers/guestReducers";
import {
  pnotifyHostReducer,
  sprebookReducer,
} from "./reducers/prebookReducers";
import {
  staffApproveReducer,
  staffDelayReducer,
  staffDetailsReducer,
  staffLoginReducer,
  staffRejectReducer,
} from "./reducers/staffReducers";

const reducer = combineReducers({
  newGuest: guestReducer,
  returningGuest: returningGuestReducer,
  aGuest: addReturningGuestReducer,
  bookedGuest: bookedGuestReducer,
  adminLogin: adminLoginReducer,
  profile: adminDetailsReducer,
  adminregister: adminRegisterReducer,
  vmslogs: logReducer,
  staffLogs: staffLogReducer,
  singleGuest: singleGuestReducer,
  notifyH: notifyHostReducer,
  checkin: checkinReducer,
  checkout: checkoutReducer,
  book: prebookReducer,
  sbook: sprebookReducer,
  addStaff: addStaffReducer,
  getStaff: getStaffReducer,
  verify: checkGuestReducer,
  pNotify: pnotifyHostReducer,
  staffLogin: staffLoginReducer,
  staffProfile: staffDetailsReducer,
  staffApprove: staffApproveReducer,
  staffDelay: staffDelayReducer,
  staffReject: staffRejectReducer,
  logout: signoutReducer,
  deleteAdmin: deleteAdminReducer,
  editAdmin: editAdminReducer,
});

const userInfoFromStorage = localStorage.getItem("userInfo")
  ? JSON.parse(localStorage.getItem("userInfo"))
  : null;
const logsFromStorage = localStorage.getItem("logs")
  ? JSON.parse(localStorage.getItem("logs"))
  : null;
const staffInfoFromStorage = localStorage.getItem("staffInfo")
  ? JSON.parse(localStorage.getItem("staffInfo"))
  : null;
const slogsFromStorage = localStorage.getItem("stafflogs")
  ? JSON.parse(localStorage.getItem("stafflogs"))
  : null;

const initialState = {
  adminLogin: { adminInfo: userInfoFromStorage },
  staffLogin: { staffInfo: staffInfoFromStorage },
  vmslogs: { logs: logsFromStorage },
  staffLogs: { logs: slogsFromStorage },
};

const middleware = [thunk];

const store = createStore(
  reducer,
  initialState,
  composeWithDevTools(applyMiddleware(...middleware))
);

export default store;
