import React, { useState, useEffect } from "react";
import Navbar from "./Navbar";
import styles from "./styles.module.css";
import {
  CircularProgress,
  Alert,
  AlertIcon,
  Center,
  useToast,
} from "@chakra-ui/react";
import { useDispatch, useSelector } from "react-redux";
import { prebookNewGuest } from "../../redux/actions/guestActions";
import { PREBOOK_RESET } from "../../redux/constants/guestConstants";
import { getStaffDetails } from "../../redux/actions/staffActions";

const StaffPrebook = () => {
  const toast = useToast();
  const [fullname, setFullName] = useState("");
  const [company, setCompany] = useState("");
  const [email, setEmail] = useState("");
  const [mobile, setMobile] = useState("");

  const [timeIn, setTime] = useState("");
  const [date, setDate] = useState("");

  const dispatch = useDispatch();

  const book = useSelector((state) => state.book);
  const { loading, error, success } = book;

  if (success) {
    toast({
      title: "Notification",
      description: "Guest has been prebooked",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: PREBOOK_RESET });
  }

  useEffect(() => {
    dispatch(getStaffDetails());
  }, [dispatch]);

  const staffProfile = useSelector((state) => state.staffProfile);
  const { staff } = staffProfile;

  const host = staff._id;

  const submitHandler = (e) => {
    e.preventDefault();
    dispatch(
      prebookNewGuest(fullname, company, email, mobile, host, timeIn, date)
    );
  };

  return (
    <div>
      <Navbar />
      <div className={styles.contents}>
        <div className={styles.overlay}>
          <small>Pre-Book a Guest</small>
        </div>
        {loading ? (
          <Center>
            <CircularProgress isIndeterminate color="green.300" />
          </Center>
        ) : (
          <div className={styles.guestContainer}>
            {error && (
              <Alert>
                <AlertIcon />
                {error}
              </Alert>
            )}
            <form onSubmit={submitHandler}>
              <div className={styles.rows}>
                <div className={styles.inputContainer}>
                  <label>Name</label>
                  <input
                    type="text"
                    onChange={(e) => setFullName(e.target.value)}
                    className="form-control"
                  />
                </div>
                <div className={styles.inputContainer}>
                  <label>Company</label>
                  <input
                    type="text"
                    onChange={(e) => setCompany(e.target.value)}
                    className="form-control"
                  />
                </div>
                <div className={styles.inputContainer}>
                  <label>Email</label>
                  <input
                    type="email"
                    onChange={(e) => setEmail(e.target.value)}
                    className="form-control"
                  />
                </div>
                <div className={styles.inputContainer}>
                  <label>Mobile</label>
                  <input
                    type="tel"
                    onChange={(e) => setMobile(e.target.value)}
                    className="form-control"
                  />
                </div>

                <div className={styles.inputContainer}>
                  <label>Date</label>
                  <input
                    type="date"
                    onChange={(e) => setDate(e.target.value)}
                    className="form-control"
                  />
                </div>
                <div className={styles.inputContainer}>
                  <label>Time</label>
                  <input
                    type="time"
                    onChange={(e) => setTime(e.target.value)}
                    className="form-control"
                  />
                </div>
                <div className={styles.inputContainer}>
                  <label style={{ visibility: "hidden" }}>Submit</label>
                  <input
                    type="submit"
                    className={`${styles.darkBlue}`}
                    value="Prebook Guest"
                  />
                </div>
              </div>
            </form>
          </div>
        )}
      </div>
    </div>
  );
};

export default StaffPrebook;
