import React, { useState, useEffect } from "react";
import Navbar from "./Navbar";
import styles from "./styles.module.css";
import {
  CircularProgress,
  Alert,
  AlertIcon,
  Center,
  useToast,
  Table,
  Thead,
  Tbody,
  Tr,
  Th,
  Td,
  TableCaption,
  Button,
  Drawer,
  DrawerBody,
  DrawerFooter,
  DrawerHeader,
  DrawerOverlay,
  DrawerContent,
  DrawerCloseButton,
  useDisclosure,
} from "@chakra-ui/react";
import { useDispatch, useSelector } from "react-redux";
import {
  ADMIN_REGISTER_RESET,
  DELETE_ADMIN_RESET,
} from "../../redux/constants/adminConstants";
import { deleteAdmins, register } from "../../redux/actions/adminActions";
import { getlogs } from "../../redux/actions/logActions";
import ReactHTMLTableToExcel from "react-html-table-to-excel";

const AdminScreen = () => {
  const toast = useToast();
  const [firstname, setFirstname] = useState("");
  const [lastname, setLastname] = useState("");
  const [email, setEmail] = useState("");
  const [mobile, setMobile] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [msg, setMsg] = useState(false);
  const dispatch = useDispatch();

  const { isOpen, onOpen, onClose } = useDisclosure();
  const btnRef = React.useRef();

  const submitHandler = (e) => {
    e.preventDefault();
    if (password !== confirmPassword) {
      setMsg(true);
    } else {
      setMsg(false);
      dispatch(register(firstname, lastname, email, password, mobile));
    }
  };

  const adminregister = useSelector((state) => state.adminregister);
  const { loading, error, success } = adminregister;

  if (success) {
    toast({
      title: "Notification",
      description: "Admin has been added",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    onClose();
    dispatch({ type: ADMIN_REGISTER_RESET });
    var date = new Date().toJSON().slice(0, 10).replace(/-/g, "/");
    dispatch(getlogs(date));
  }

  const vmslogs = useSelector((state) => state.vmslogs);
  const { logs, loading: loadingLogs, error: errorLogs } = vmslogs;

  useEffect(() => {
    var date = new Date().toJSON().slice(0, 10).replace(/-/g, "/");
    dispatch(getlogs(date));
  }, [dispatch, date]);

  const deleteAdmin = useSelector((state) => state.deleteAdmin);
  const { success: Dsuccess, loading: Dloading, error: Derror } = deleteAdmin;
  const deleteHandler = (id) => {
    if (window.confirm("Delete this User?")) {
      dispatch(deleteAdmins(id));
    }
  };

  if (Dsuccess) {
    toast({
      title: "Notification",
      description: "Admin has been deleted",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: DELETE_ADMIN_RESET });
    var date = new Date().toJSON().slice(0, 10).replace(/-/g, "/");
    dispatch(getlogs(date));
  }
  return (
    <div>
      <Navbar />
      <div className={styles.contents}>
        <div className={styles.overlay}>
          <small>Add Adminstrator</small>
        </div>
        <Drawer
          isOpen={isOpen}
          placement="right"
          onClose={onClose}
          finalFocusRef={btnRef}
          size="md"
        >
          <DrawerOverlay />
          <DrawerContent>
            <DrawerCloseButton />
            <DrawerHeader>Create your account</DrawerHeader>

            <DrawerBody>
              {loading ? (
                <Center>
                  <CircularProgress isIndeterminate color="green.300" />
                </Center>
              ) : (
                <>
                  {msg && (
                    <Alert status="error">
                      <AlertIcon />
                      Password does not match
                    </Alert>
                  )}
                  {error && (
                    <Alert status="error">
                      <AlertIcon />
                      {error}
                    </Alert>
                  )}
                  <div className={styles.inputContainer}>
                    <label>First Name</label>
                    <input
                      type="text"
                      className="form-control"
                      onChange={(e) => setFirstname(e.target.value)}
                      value={firstname}
                      placeholder="First Name"
                    />
                  </div>
                  <div className={styles.inputContainer}>
                    <label>Last Name</label>
                    <input
                      type="text"
                      className="form-control"
                      onChange={(e) => setLastname(e.target.value)}
                      value={lastname}
                      placeholder="Last Name"
                    />
                  </div>
                  <div className={styles.inputContainer}>
                    <label>Email</label>
                    <input
                      type="email"
                      className="form-control"
                      onChange={(e) => setEmail(e.target.value)}
                      value={email}
                      placeholder="Email"
                    />
                  </div>
                  <div className={styles.inputContainer}>
                    <label>Mobile</label>
                    <input
                      type="number"
                      className="form-control"
                      onChange={(e) => setMobile(e.target.value.slice(0, 11))}
                      value={mobile}
                      placeholder="Mobile"
                    />
                  </div>

                  <div className={styles.inputContainer}>
                    <label>Password</label>
                    <input
                      type="password"
                      className="form-control"
                      onChange={(e) => setPassword(e.target.value)}
                      value={password}
                      placeholder="Password"
                    />
                  </div>
                  <div className={styles.inputContainer}>
                    <label>Confirm Password</label>
                    <input
                      type="password"
                      className="form-control"
                      onChange={(e) => setConfirmPassword(e.target.value)}
                      value={confirmPassword}
                      placeholder="Confirm Password"
                    />
                  </div>
                </>
              )}
            </DrawerBody>

            <DrawerFooter>
              <Button variant="outline" mr={3} onClick={onClose}>
                Cancel
              </Button>
              <Button colorScheme="blue" type="button" onClick={submitHandler}>
                Add Admin
              </Button>
            </DrawerFooter>
          </DrawerContent>
        </Drawer>

        <div className={styles.guestContainer}>
          <Button ref={btnRef} colorScheme="teal" onClick={onOpen}>
            Add Admin
          </Button>
          <br />
          <br />
          <br />
          <hr />
          {loadingLogs || Dloading ? (
            <Center>
              <CircularProgress isIndeterminate color="green.300" />
            </Center>
          ) : (
            <>
              {errorLogs && (
                <Alert>
                  <AlertIcon />
                  {errorLogs}
                </Alert>
              )}
              {Derror && (
                <Alert>
                  <AlertIcon />
                  {Derror}
                </Alert>
              )}
              {logs && logs.allAdmin.length <= 0 ? (
                <>
                  <Alert status="warning">
                    <AlertIcon />
                    No Guest Available
                  </Alert>
                </>
              ) : (
                <>
                  <Button colorScheme="yellow">
                    <ReactHTMLTableToExcel
                      id="test-table-xls-button"
                      className="download-table-xls-button"
                      table="table-to-xls"
                      filename="Visitors Log"
                      sheet="tablexls"
                      buttonText="Download as XLS"
                    />
                  </Button>
                  <Table variant="simple" id="table-to-xls">
                    <TableCaption>All Admins</TableCaption>
                    <Thead>
                      <Tr>
                        <Th>S/N</Th>
                        <Th>Name</Th>
                        <Th>Email</Th>
                        <Th>Mobile</Th>
                        <Th>Actions</Th>
                      </Tr>
                    </Thead>
                    <Tbody>
                      {logs &&
                        logs.allAdmin.map((item, index) => (
                          <Tr key={index}>
                            <Td>{index + 1}</Td>
                            <Td>{item.firstname + " " + item.lastname}</Td>
                            <Td>{item.email}</Td>
                            <Td>{item.mobile}</Td>
                            <Td>
                              <Button colorScheme="yellow">Edit</Button>
                              <Button
                                colorScheme="red"
                                type="button"
                                onClick={() => deleteHandler(item._id)}
                              >
                                Delete
                              </Button>
                            </Td>
                          </Tr>
                        ))}
                    </Tbody>
                  </Table>
                </>
              )}
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default AdminScreen;
