import React from 'react'
import ReactECharts from "echarts-for-react";

const Chart = ({ logs }) => {
    const option = {
        tooltip: {
            trigger: "item",
        },

        legend: {
            top: "5%",
            left: "center",
        },
        series: [
            {
                name: "Logs",
                type: "pie",
                radius: ["40%", "70%"],
                avoidLabelOverlap: false,
                itemStyle: {
                    borderRadius: 10,
                    borderColor: "#fff",
                    borderWidth: 2,

                },
                label: {
                    show: false,
                    position: "center",
                },
                emphasis: {

                    label: {
                        show: true,
                        fontSize: "20",
                        fontWeight: "bold",
                    },
                },
                labelLine: {
                    show: false,
                },
                data: [
                    { value: logs && logs.vtoday, name: "Visitor's Today" },
                    { value: logs && logs.pending, name: "Pending" },
                    { value: logs && logs.checkedIn, name: "Checkedin Visitors" },
                    { value: logs && logs.checkedOut, name: "ChedkedOut Visitors" },

                ],
            },
        ],
    };
    return (
        <div>
            <ReactECharts option={option} style={{ height: '500px', width: '100%' }} />
        </div>
    )
}

export default Chart
