import React, { useState } from "react";
import Navigation from "../components/Navigation";
import styles from "./styles.module.css";
import { Link, useNavigate } from "react-router-dom";
import Slider from "../components/Slider";
import { FiArrowRightCircle, FiArrowLeftCircle } from "react-icons/fi";
import { useSelector, useDispatch } from "react-redux";
import {
  Alert,
  AlertIcon,
  Center,
  CircularProgress,
  useToast,
} from "@chakra-ui/react";
import Badge from "../components/Badge";
import { signoutUser } from "../redux/actions/guestActions";
import { SIGNOUT_RESET } from "../redux/constants/guestConstants";

const SignOut = () => {
  let navigate = useNavigate();
  let toast = useToast();

  const [mobile, setMobile] = useState("");
  const [msg, setMsg] = useState(false);

  const dispatch = useDispatch();
  const submitHandler = (e) => {
    e.preventDefault();
    if (!mobile) {
      setMsg(true);
    } else {
      setMsg(false);
      dispatch(signoutUser(mobile));
    }
  };
  const logout = useSelector((state) => state.logout);
  const { loading, error, success } = logout;

  if (success) {
    navigate("/");
    toast({
      title: "Notification",
      description: "Signout Successfull",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: SIGNOUT_RESET });
  }

  return (
    <div>
      <Badge />
      <Navigation />
      <div className={styles.contentx}>
        <div className={styles.formPadding}>
          <h3>Hello there!</h3>
          {loading ? (
            <>
              <Center>
                <CircularProgress isIndeterminate color="green.300" />
              </Center>
            </>
          ) : (
            <>
              <Slider size="0" />
              <form onSubmit={submitHandler}>
                {error && (
                  <Alert status="error">
                    <AlertIcon />
                    {error}
                  </Alert>
                )}
                {msg && (
                  <Alert status="warning">
                    <AlertIcon />
                    Enter Mobile Number
                  </Alert>
                )}

                <div className={styles.inputContainer}>
                  <label>Enter Mobile Number</label>
                  <input
                    type="tel"
                    className="form-control"
                    onChange={(e) => setMobile(e.target.value)}
                    placeholder="Enter Mobile"
                    value={mobile}
                  />
                </div>
                <div className={styles.inputContainer}>
                  <div className={styles.left}>
                    <Link
                      to="/"
                      className={`${styles.customBtn} ${styles.white}`}
                    >
                      <FiArrowLeftCircle /> Back
                    </Link>
                  </div>
                  <div className={styles.right}>
                    <button
                      type="submit"
                      className={`${styles.customBtn} ${styles.green}`}
                      value="Next"
                    >
                      <FiArrowRightCircle /> Signout
                    </button>
                  </div>
                </div>
              </form>
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default SignOut;
