import React, { useEffect } from "react";
import Cards from "./UI/Cards";
import Navbar from "./Navbar";
import styles from "./styles.module.css";
import {
  IoPeopleSharp,
  IoWalk,
  IoCheckmarkCircle,
  IoArrowRedo,
  IoBriefcase,
  IoCalendarOutline,
  IoBusSharp,
} from "react-icons/io5";
import { useDispatch, useSelector } from "react-redux";
import { getlogs } from "../../redux/actions/logActions";
import { getAdminDetails } from "../../redux/actions/adminActions";
import Chart from "./UI/Chart";
import { Link } from "react-router-dom";

const Dashboard = () => {
  const dispatch = useDispatch();

  const vmslogs = useSelector((state) => state.vmslogs);
  const { logs } = vmslogs;

  useEffect(() => {
    var date = new Date().toJSON().slice(0, 10).replace(/-/g, "/");
    dispatch(getlogs(date));
    dispatch(getAdminDetails());
  }, [dispatch]);

  //   const vmslogs = useSelector((state) => state.vmslogs);
  //   const { logs } = vmslogs;
  return (
    <div>
      <Navbar />
      <div className={styles.contents}>
        <div className={styles.overlay}>
          <div className={styles.title}>
            <small>Dashboard</small>
          </div>

          <div className={styles.cardContainer}>
            <div className={styles.cardGrid}>
              <Link to="/frontdesk/logs/today">
                <Cards
                  title="Vistors Today"
                  count={logs && logs.vtoday}
                  color={styles.cyan}
                >
                  <IoWalk />
                </Cards>
              </Link>
              <Link to="/frontdesk/awaitingHost">
                <Cards
                  title="Pending Visitors Today"
                  count={logs && logs.pending}
                  color={styles.purple}
                >
                  <IoBusSharp />
                </Cards>
              </Link>
              <Link to="/frontdesk/checkedin">
                <Cards
                  title="Checked-In Today"
                  count={logs && logs.checkedIn}
                  color={styles.gold}
                >
                  <IoCheckmarkCircle />
                </Cards>
              </Link>
              <Link to="/frontdesk/checkedout">
                <Cards
                  title="Checked-Out Today"
                  count={logs && logs.checkedOut}
                  color={styles.red}
                >
                  <IoArrowRedo />
                </Cards>
              </Link>
              <Link to="/frontdesk/prebook">
                <Cards
                  title="PreBooked Guests"
                  count={logs && logs.book}
                  color={styles.purple}
                >
                  <IoBriefcase />
                </Cards>
              </Link>
              <Link to="/frontdesk/logs">
                <Cards
                  title="All Visitors"
                  count={logs && logs.all}
                  color={styles.gold}
                >
                  <IoCalendarOutline />
                </Cards>
              </Link>
              <Link to="/frontdesk/staff">
                <Cards
                  title="All Staff"
                  count={logs && logs.staff}
                  color={styles.red}
                >
                  <IoPeopleSharp />
                </Cards>
              </Link>

              <Link to="/frontdesk/admin">
                <Cards
                  title="Available Admin"
                  count={logs && logs.admin}
                  color={styles.cyan}
                >
                  <IoCalendarOutline />
                </Cards>
              </Link>
            </div>
          </div>
        </div>
        <Chart logs={logs} />
      </div>
    </div>
  );
};

export default Dashboard;
