import React, { useEffect } from "react";
import Cards from "./UI/Cards";
import Navbar from "./Navbar";
import styles from "./styles.module.css";
import {
  // IoPeopleSharp,
  IoWalk,
  IoCheckmarkCircle,
  IoArrowRedo,
  IoBriefcase,
  IoCalendarOutline,
  IoBusSharp,
} from "react-icons/io5";
import { useDispatch, useSelector } from "react-redux";
import { getStafflogs } from "../../redux/actions/logActions";
import { getStaffDetails } from "../../redux/actions/staffActions";
import {
  Table,
  Thead,
  Tbody,
  Tr,
  Th,
  Td,
  TableCaption,
} from "@chakra-ui/react";
import { Link } from "react-router-dom";

const StaffDashboard = () => {
  const dispatch = useDispatch();

  const staffLogs = useSelector((state) => state.staffLogs);
  const { logs, newGuest, approved, rejected } = staffLogs;

  useEffect(() => {
    var date = new Date().toJSON().slice(0, 10).replace(/-/g, "/");
    dispatch(getStafflogs(date));
    dispatch(getStaffDetails());
  }, [dispatch]);

  //   const vmslogs = useSelector((state) => state.vmslogs);
  //   const { logs } = vmslogs;
  return (
    <div>
      <Navbar />
      <div className={styles.contents}>
        <div className={styles.overlay}>
          <div className={styles.title}>
            <small>Dashboard</small>
          </div>
          <div className={styles.cardContainer}>
            <div className={styles.cardGrid}>
              <Cards
                title="Vistors Today"
                count={logs && logs.vtoday}
                color={styles.cyan}
              >
                <IoWalk />
              </Cards>
              <Cards
                title="Pending Visitors"
                count={logs && logs.pending}
                color={styles.purple}
              >
                <IoBusSharp />
              </Cards>
              <Cards
                title="Approved Visitor"
                count={approved && approved.length}
                color={styles.gold}
              >
                <IoCheckmarkCircle />
              </Cards>
              <Cards
                title="Rejected Visitor"
                count={rejected && rejected.length}
                color={styles.red}
              >
                <IoArrowRedo />
              </Cards>
              <Cards
                title="PreBooked Guests"
                count={logs && logs.prebook}
                color={styles.purple}
              >
                <IoBriefcase />
              </Cards>
              <Cards
                title="All Visitors"
                count={logs && logs.all}
                color={styles.gold}
              >
                <IoCalendarOutline />
              </Cards>
            </div>
          </div>
        </div>

        <Table variant="simple">
          <TableCaption>My Guest</TableCaption>
          <Thead>
            <Tr>
              <Th>Name</Th>
              <Th>Company</Th>
              <Th>Email</Th>
              <Th>Mobile</Th>
              <Th>Purpose Of Visit</Th>
              <Th>Actions</Th>
            </Tr>
          </Thead>
          <Tbody>
            {newGuest &&
              newGuest.map((item) => (
                <Tr>
                  <Td>{item.user.fullname}</Td>
                  <Td>{item.user.company}</Td>
                  <Td>{item.user.email}</Td>
                  <Td>{item.user.mobile}</Td>
                  <Td>{item.purpose}</Td>
                  <Td>
                    <Link
                      className={`${styles.btnSm} ${styles.red}`}
                      to={`/staff/guest/${item._id}`}
                    >
                      Click Here
                    </Link>
                  </Td>
                </Tr>
              ))}
          </Tbody>
        </Table>
      </div>
    </div>
  );
};

export default StaffDashboard;
